import React, { useEffect, useState } from 'react';
import fetch from 'node-fetch'



function HomePage() {
  const [data, setData] = useState([])
  
  useEffect(() => {
    fetch('http://localhost:3001/users/all')
    .then(res => {
      return res.json()
    })
    .then(result => {
      const newData = data
      result.forEach(item => {
        newData.push(item)
      })
      
      return newData
    })
    .then(
      newData => {
        // console.log(newData)
        setData(newData)
      }
    )
  });

  return (
  <div>
    Welcome to Next.js!
    <div>
      {data.forEach(item => console.log(item))}
    </div>
  </div>
  )
}

export default HomePage